import React from "react"
import { graphql } from "gatsby"
// import { Link, graphql } from "gatsby"
// import WeatherIcon from 'react-icons-weather';
import Layout from "../components/layout"
import '../components/index.css'

export default ({ data }) => {
  const iconName = data.allWeatherData.totalCount
  return (
    <Layout>
      <h1>About</h1> 
      <canvas id="rain" width="64" height="64"></canvas>
      {console.log(iconName)}
      {data.allWeatherData.edges.map(({ node }) => (
        <div className="weather" key={[node.id]}>
          <div className="info">
              <p className="p temp" dangerouslySetInnerHTML={{__html: `Tempreature:  ${node.currently.temperature}*`}}/> 
              <p className="p summary" dangerouslySetInnerHTML={{__html: node.currently.summary}}/> 
              <p className="p pressure" dangerouslySetInnerHTML={{__html: `Pressure: ${node.currently.pressure} Pa`}}/> 
              <p className="p visibility" dangerouslySetInnerHTML={{__html: `Visibility: ${node.currently.visibility} Km`}}/> 
          </div>
          {/* <div className="icon">
            <WeatherIcon name="owm" iconId="200" flip="horizontal" rotate="90" size="lg" /> 
          </div> */}
        </div>
      ))}
    </Layout>
  )
}
export const query = graphql`
{
  allWeatherData {
   totalCount
   edges {
     node {
      offset
       id
       currently {
         summary
         icon
         temperature
         pressure
         visibility
       }
     }
   }
 }
}
`